.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: none;
  background: rgba(0, 0, 0, 0.5);

  input {
    width: 100%;
    padding: 5px 15px;
    margin: 5px 0;
    border: 1px solid #16b14d;
  }
}

.modalContent {
  padding: 25px;
  background: white;
  border-radius: 16px;
  min-width: 250px;
}

.modal.active {
  display: flex;
  justify-content: center;
  align-items: center;
}

